		// 查询选中的当天数据
		searchData() {
			// 不通的数据请求参数不通
			if (this.Inv == 0) {
				this.type = 1
			}
			if (this.Inv == 1) {
				this.type = 2
			}
			if (this.Inv == 2) {
				this.type = 3
			}
			let obj = {
				time: this.pickerDate,
				page: this.page,
				limit: this.limit,
				type: this.type
			}
			// console.log(obj)
			this.$common.requests('/api/index/getoneday', obj).then(res => {
				this.showLoadMore = false;
				if (res.data.code == 1) {
					this.lisize = res.data.data.length;
					if (this.lisize == 0) return;
					if (this.page == 1) {
						// 请求成功,不通数据不通变量接收渲染
						if (this.Inv == 0) {
							this.datalist = res.data.data
						}
						if (this.Inv == 1) {
							this.validdatalist = res.data.data
						}
						if (this.Inv == 2) {
							this.garbagedatalist = res.data.data
						}
					} else {
						// 不通数据对应不通的下拉刷新，添加数据进对应变量数组
						if (this.Inv == 0) {
							this.datalist = this.datalist.concat(res.data.data);
						}
						if (this.Inv == 1) {
							this.validdatalist = this.validdatalist.concat(res.data.data);
						}
						if (this.Inv == 2) {
							this.garbagedatalist = this.garbagedatalist.concat(res.data.data);

						}

					}
				}
				if (res.data.code == 0) {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			})

		},
