		// 弹出模态框点击确定就删除选中数据
		tureclick() {
			this.isShow = !this.isShow
			var chooseid = ''
			// 判断是那个数据列表要删除
			// 后台要求删除多个选中id之间用,隔开
			if (this.Inv == 0) {
				this.datalist.forEach(item => {
					if (item.checked == true) {
						chooseid += (item.id + ',')
					}
				})
			}
			if (this.Inv == 1) {
				this.validdatalist.forEach(item => {
					if (item.checked == true) {
						chooseid += (item.id + ',')
					}
				})
			}
			if (this.Inv == 2) {
				this.garbagedatalist.forEach(item => {
					if (item.checked == true) {
						chooseid += (item.id + ',')
					}
				})
			}
			chooseid = chooseid.substring(0, chooseid.length - 1)
			// 当选中是空就是删除所有数据
			if (chooseid == '') {
				if (this.Inv == 0) {
					this.datalist.forEach(item => {
						chooseid += (item.id + ',')
					})
				}
				if (this.Inv == 1) {
					this.validdatalist.forEach(item => {
						chooseid += (item.id + ',')
					})
				}
			}
			var obj = {
				ids: chooseid
			}
			this.$common.requests('/api/record/del_record', obj).then(res => {
				uni.showToast({
					title: res.data.msg,
					duration: 1000
				});
				// 删除数据数据成功，立刻显示最新数据数据
				// 请求数据分析方法
				this.searchData()
			})
		},

		// 查看报表
		// 跳转显示折现图页面
		lookreport() {
			var checkedid = ''
			// 后台要求多个选中id之间用,隔开
			// 最佳数据
			if (this.Inv == 0) {
				this.datalist.forEach(item => {
					if (item.checked == true) {
						checkedid += (item.id + ',')
					}
				})
				checkedid = checkedid.substring(0, checkedid.length - 1)
				// 如果没选中一个,就全选
				if (checkedid == '') {
					this.datalist.forEach(item => {
						if (item.checked == false) {
							checkedid += (item.id + ',')
						}
					})
				}
				// console.log(checkedid,'数据没传到下一个页面?')
				// 把当前选中id带到折线图页面
				uni.navigateTo({
					url: '../bestlinechart/bestlinechart?checkedid=' + checkedid + '&pickerDate=' + this.pickerDate + '&Inv=0'
				})
			}
			if (this.Inv == 1) {
				this.validdatalist.forEach(item => {
					if (item.checked == true) {
						checkedid += (item.id + ',')
					}
				})
				checkedid = checkedid.substring(0, checkedid.length - 1)
				// 如果没选中一个,就全选
				if (checkedid == '') {
					this.validdatalist.forEach(item => {
						if (item.checked == false) {
							checkedid += (item.id + ',')
						}
					})
				}
				// console.log(checkedid,'数据没传到下一个页面?')
				// 把当前选中id带到折线图页面
				uni.navigateTo({
					url: '../linechart/linechart?checkedid=' + checkedid + '&pickerDate=' + this.pickerDate + '&Inv=1'
				})
			}

